import {fileUpload, inputPhone, inputEmail} from "../services/const.js";
import {validatePhone, validateEmail} from "../services/validate.js";
import Cleave from "cleave.js";

function modalWindow() {
    fileUpload.addEventListener("change", function (event) {
        const fileName =
            event.target.files.length > 0
                ? event.target.files[0].name
                : "  ";
        document.getElementById("fileName").innerHTML = fileName;
        document.getElementById("fileName").style.cssText =
            "font-size: 10px; align-items: center; justify-content: center;";
    });

    const cleave = new Cleave(inputPhone, {
        delimiters: ["(", ") ", "-", "-"],
        blocks: [0, 3, 3, 2, 2],
        prefix: "",
        rawValueTrimPrefix: false,
        numericOnly: true,
    });
    inputPhone.addEventListener("input", validatePhone);
    inputEmail.addEventListener("input", validateEmail);
}

export default modalWindow;
