"use strict";

import preloader from "./modules/preloader.js";
import slider from "./modules/slider.js";
import changeTablets from "./modules/tablets.js";
import menuScroll from "./modules/menu.js";
import modalWindow from "./modules/modal.js";
import {
    spaceSelectClick,
    spaceSelectListClick,
    hamburgerClick,
    requestClick,
    modalFormSubmitClick,
    modalFormThanksClick
} from "./services/clickFunctions.js";
import click from "./services/click.js";
import resize from "./services/resize.js";
import scroll from "./services/scroll.js";

import {
    changeTelMail,
    changeFooterTitle,
    changeImgTitle,
    changeLoremTitle,
    changeTitleModal,
} from "./services/changeDomElements.js";

document.addEventListener("DOMContentLoaded", () => {
    // preloader
    preloader();

    changeTablets("mouseover");
    changeTablets("mouseout");
    modalWindow();

    changeTelMail();
    changeFooterTitle();
    changeImgTitle();
    changeLoremTitle();
    changeTitleModal();

    slider(".swiper-container");

    //click
    click(spaceSelectClick, spaceSelectListClick, hamburgerClick,
        requestClick,
        modalFormSubmitClick, modalFormThanksClick);

    //resize
    resize(changeTelMail, changeFooterTitle, changeImgTitle, changeLoremTitle,
        changeTitleModal,);

    //scroll
    scroll(menuScroll);
});
