import Swiper from 'swiper/bundle';
import {Navigation, Pagination} from "swiper/modules";
import {num, slides} from "../services/const.js";

function slider(className) {
    if (className == ".swiper-container") {
        num[1].innerHTML = `0${slides.length}`;
        const swiper = new Swiper(className, {
            modules: [Navigation, Pagination],
            slidesPerView: "auto",
            loop: true,
            spaceBetween: 0,
            pagination: {
                el: ".swiper-pagination",
                type: "progressbar",
            },
            navigation: {
                nextEl: ".swiper-button-next",
                prevEl: ".swiper-button-prev",
            },
        });

        swiper.on("slideChangeTransitionEnd", () => {
            const activeSlide = document.querySelector(".swiper-slide-active");
            const currentSlideNumber = activeSlide.getAttribute(
                "data-swiper-slide-index"
            );
            num[0].innerHTML = `0${+currentSlideNumber + 1}`;
        });
    } else if (className == ".philosophy__right-container") {
        num[1].innerHTML = `0${slides.length}`;
        const swiper = new Swiper(className, {
            modules: [Navigation, Pagination],
            slidesPerView: "auto",
            loop: true,
            spaceBetween: 12,
            pagination: {
                el: ".swiper-pagination",
                type: "progressbar",
            },
            navigation: {
                nextEl: ".swiper-button-next",
                prevEl: ".swiper-button-prev",
            },
        });

        swiper.on("slideChangeTransitionEnd", () => {
            const activeSlide = document.querySelector(".swiper-slide-active");
            const currentSlideNumber = activeSlide.getAttribute(
                "data-swiper-slide-index"
            );
            num[0].innerHTML = `0${+currentSlideNumber + 1}`;
        });
    } else if (className == ".new-slider__swiper-container") {
        const swiper = new Swiper(className, {
            slidesPerView: "auto",
            loop: false,
            breakpoints: {
                1440: {
                    spaceBetween: 48,
                },
                1024: {
                    spaceBetween: 40,
                },
                768: {
                    spaceBetween: 20,
                },
                375: {
                    spaceBetween: 26,
                },
            },
        });
    }
}

export default slider;
